    .wrapper{
        width: 100%;
        height: 95vh;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #D9D9D9;
    }
    .inner{
        display: flex;
        padding: 40px;
        border-radius: 50%;
        background-color: #AABFAF;
        flex-direction: column;
        align-items: center;
        
    }
    .number {
            padding-top: 10px;
            font-size: 86px;
            color:#592A19;
    }
    .page {
        font-size: 34px;
        line-height: 2;
        color: #261007;
    }
    .cat{
        padding-top: 10px;
        font-size: 20px;
        color: #A66B49;
    }